/** Word rotation start **/
.animation_word-rotation-mask .animated_text {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 60px;
}

.animation_word-rotation-mask span.animated_text[data-show] {
    top: 0;
    transform: translateY(-10%);
    transition: .5s all ease-in-out;
}

.animation_word-rotation-mask span.animated_text[data-up] {
    transform: translateY(-200%);
    transition: .5s all ease-in-out;
}

@media screen and (max-width: 526px) and (min-width: 480px) {
    .animation_word-rotation-mask.for-residents-hero {
        height: 110px;
    }

    .animation_word-rotation-mask .animated_text {
        top: 110px;
    }

    .animation_word-rotation-mask span.animated_text[data-show] {
        top: 0;
    }

    .display-block-mobile {
        display: block;
    }
}

@media screen and (max-width: 479px) {
    .animation_word-rotation-mask .animated_text {
        top: 50px;
    }
}

/** Word rotation end **/
/** Homeowners' hero section start **/
@media screen and (max-width: 767px) and (min-width: 480px) {
    .section_residents-hero .section_subheader {
        max-width: 80%;
    }
}

/** Homeowners' hero section end **/